import React from "react";
import { Card } from "react-bootstrap";
import PropTypes from "prop-types";
import moment from "moment";
import CallButton from "../../pages/Doctor/Appointment/TeleConsultation/CallButton";

export default function GenericPatientCard(props) {
  const {
    name,
    genderInfo,
    gender,
    dobInfo,
    dob,
    professionInfo,
    profession,
    currentLocationInfo,
    currentLocation,
    patientLocalGPInfo,
    patientLocalGP,
    emailInfo,
    email,
    phoneInfo,
    phone,
    specialityInfo,
    speciality,
    pharmacyInfo,
    pharmacy,
    locationInfo,
    location,
    nextOfKinName,
    nextOfKinNumber,
    nextOfKinNameInfo,
    nextOfKinNumberInfo,
    image,
    callButton,
  } = props;

  return (
    <Card className="generic-card">
      <Card.Body className="p-0 main-personalprofile">
        {/* <img
            src={IMAGES.BG_BOTTOM_ROUNDED}
            alt="Background Rounded"
            className="w-100"
          /> */}
        <div className="img-card w-100">
          {/* <img
            src={image ? image : `https://ui-avatars.com/api/?name=${`${name}`}&background=6045eb&color=fff`}
            alt="tele-consultation patient"
            className="img-fluid Profile-2 position-relative test-border"
          /> */}
          <div className="upload_pic profile_upload profileContent">
            <img
              src={
                image
                  ? image
                  : `https://ui-avatars.com/api/?name=${`${name}`}&background=6045eb&color=fff`
              }
              alt=""
              className="img-fluid Profile-2 position-relative test-border"
            />
          </div>
          <p
            className="text-center my-3"
            style={{ fontSize: "1.5rem", fontWeight: 500, paddingTop: "40px" }}
          >
            {name ? name : "N/A"}
          </p>
        </div>
        <div className="px-4 card-content pt-4">
          {genderInfo && (
            <>
              {" "}
              <span className="d-flex flex-wrap justify-content-between pt-3">
                <p>Gender</p>
                <p>{gender ? gender : "N/A"}</p>
              </span>
              <hr />
            </>
          )}
          {dobInfo && (
            <>
              <span className="d-flex flex-wrap justify-content-between">
                <p>D.O.B</p>
                <p>{moment(dob).format("MM/DD/YYYY")}</p>
              </span>
              <hr />
            </>
          )}
          {locationInfo && (
            <>
              <span className="d-flex flex-wrap justify-content-between">
                <p>Location</p>
                <p>{location ? location : "N/A"}</p>
              </span>
              <hr />
            </>
          )}
          {professionInfo && (
            <>
              <span className="d-flex flex-wrap justify-content-between">
                <p>Profession/Occupation:</p>
                <p>{profession ? profession : "N/A"}</p>
              </span>
              <hr />
            </>
          )}
          {currentLocationInfo && (
            <>
              <span className="d-flex flex-wrap justify-content-between">
                <p>Current Location</p>
                <p>{currentLocation ? currentLocation : "N/A"}</p>
              </span>
              <hr />
            </>
          )}
          {patientLocalGPInfo && (
            <>
              <span className="d-flex flex-wrap justify-content-between">
                <p>Patient Local GP</p>
                <p>{patientLocalGP ? patientLocalGP : "N/A"}</p>
              </span>
              <hr />
            </>
          )}
          {emailInfo && (
            <>
              <span className="d-flex flex-wrap justify-content-between">
                <p>Email</p>
                <p>{email ? email : "N/A"}</p>
              </span>
              <hr />
            </>
          )}
          {pharmacyInfo && (
            <>
              <span className="d-flex flex-wrap justify-content-between">
                <p>Pharmacy</p>
                <p>{pharmacy ? pharmacy : "N/A"}</p>
              </span>
              <hr />
            </>
          )}
          {phoneInfo && (
            <>
              <span className="d-flex flex-wrap justify-content-between">
                <p>Phone</p>
                <p>{phone ? phone : "N/A"}</p>
              </span>
              <hr />
            </>
          )}
          {nextOfKinNameInfo && (
            <>
              <span className="d-flex flex-wrap justify-content-between">
                <p>Next of Kin Name</p>
                <p>{nextOfKinName ? nextOfKinName : "N/A"}</p>
              </span>
              <hr />
            </>
          )}
          {nextOfKinNumberInfo && (
            <span className="d-flex flex-wrap justify-content-between">
              <p>Next of Kin Number</p>
              <p>{nextOfKinNumber ? nextOfKinNumber : "N/A"}</p>
            </span>
          )}
          {specialityInfo && (
            <span className="d-flex flex-wrap justify-content-between">
              <p>Speciality</p>
              <p>{speciality ? speciality : "N/A"}</p>
            </span>
          )}
          {callButton && <CallButton />}
        </div>
      </Card.Body>
    </Card>
  );
}
GenericPatientCard.propTypes = {
  name: PropTypes.node.isRequired,
  genderInfo: PropTypes.node.isRequired,
  gender: PropTypes.node.isRequired,
  dobInfo: PropTypes.node.isRequired,
  dob: PropTypes.node.isRequired,
  professionInfo: PropTypes.node.isRequired,
  profession: PropTypes.node.isRequired,
  currentLocationInfo: PropTypes.node.isRequired,
  currentLocation: PropTypes.node.isRequired,
  patientLocalGPInfo: PropTypes.node.isRequired,
  patientLocalGP: PropTypes.node.isRequired,
  emailInfo: PropTypes.node.isRequired,
  email: PropTypes.node.isRequired,
  phoneInfo: PropTypes.node.isRequired,
  phone: PropTypes.node.isRequired,
  specialityInfo: PropTypes.node.isRequired,
  speciality: PropTypes.node.isRequired,
  pharmacyInfo: PropTypes.node.isRequired,
  pharmacy: PropTypes.node.isRequired,
  locationInfo: PropTypes.node.isRequired,
  location: PropTypes.node.isRequired,
  nextOfKinNumber: PropTypes.node.isRequired,
  nextOfKinName: PropTypes.node.isRequired,
  callButton: PropTypes.element.isRequired,
};
