/* eslint-disable */
import React, { useState } from "react";
import { Button, Form, Modal } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { Device } from "@twilio/voice-sdk";
import { useDispatch, useSelector } from "react-redux";

import { twilioTokenAction } from "../../../../../app/features/twilioCall/twilioCall.slice";
import IMAGES from "../../../../../assets/images";
import {
  CALL_STATE,
  DEVICE_STATE,
} from "../../../../../components/LookupData/Data";

const CallButton = () => {
  const { register } = useForm();
  const [isCall, setIsCall] = useState(false);
  const [state, setState] = useState(DEVICE_STATE.Unregistered);
  const [callState, setCallState] = useState(null);
  const [isCallData, setIsCallData] = useState(null);

  const dispatch = useDispatch();

  const { videoCallData } = useSelector((state) => state?.vonageData || {});
  const { patientAppointedDetail } = useSelector((state) => state.appointment);

  // MAKE AN OUTGOING CALL
  async function makeOutgoingCall(device, data) {
    const deviceIdentity = data?.callingDeviceIdentity;
    var params = {
      To: patientAppointedDetail?.detailDTO?.phoneNumber,
      callingDeviceIdentity: deviceIdentity,
    };

    if (device) {
      console.log(`Attempting to call ${params.To} ...`);

      const call = await device.connect({ params });
      setIsCallData(call);
      // Code to handle a successful connection (if needed)
      if (call) {
        setCallState(CALL_STATE.Connected);
        console.log("setCallStateConnected", callState);
      }

      console.log("Connection successful:", setCallState);

      call.on("accept", function () {
        console.log(
          "'accepted' means the call has finished connecting and the state is now 'open'"
        );
        setCallState(CALL_STATE.Accept);
      });
      call.on("ringing", function () {
        console.log("Ringing ...");
        setCallState(CALL_STATE.Ringing);
      });
      call.on("disconnect", function () {
        console.log("Call Disconnected");
        setCallState(CALL_STATE.Disconnect, () => {
          console.log("callState", callState);
        });
      });

      call.on("cancel", function () {
        console.log("Call Cancelled");
        setCallState(CALL_STATE.Cancel);
      });
    } else {
      console.log("Unable to make call.");
    }
  }

  function handleHangUp() {
    if (isCallData) {
      console.log("Hanging up ...", isCallData);
      isCallData.disconnect();
    }
  }
  // SETUP STEP 2: Instantiate a new Twilio.Device
  function intitializeDevice(tokenData) {
    setState(DEVICE_STATE.Registering);
    console.log("Initializing a new device", state);
    let device = new Device(tokenData?.twilioToken, {
      logLevel: 1,
      // Set Opus as our preferred codec. Opus generally performs better, requiring less bandwidth and
      // providing better audio quality in restrained network conditions.
      codecPreferences: ["opus", "pcmu"],
    });

    addDeviceListeners(device);

    // Device must be registered in order to receive incoming calls
    device.register();
    makeOutgoingCall(device, tokenData);
  }

  // SETUP STEP 3: Listen for Twilio.Device states
  function addDeviceListeners(device) {
    device.on("registered", function () {
      console.log("The device is ready to make and receive incoming calls.");
      setState(DEVICE_STATE.Registered);
    });
    device.on("registering", function () {
      setState(DEVICE_STATE.Registering);
    });

    device.on("error", function (error) {
      console.log("Twilio.Device Error: " + error.message);
      setState(DEVICE_STATE.Error);
    });

    device.audio.on("deviceChange", function () {
      console.log("device Change");
    });
  }

  // SETUP STEP 1: Request an Access Token
  function startupClient() {
    console.log("Requesting Access Token...");
    const tokenData = {
      twilioToken: videoCallData?.twilioToken,
      callingDeviceIdentity: videoCallData?.twilioIdentity,
    };
    intitializeDevice(tokenData);
  }

  function createTwilioFn() {
    dispatch(twilioTokenAction(startupClient));
  }
  return (
    <>
      <div className="w-100 d-flex mb-3">
        <Button
          style={{ background: "#6045EB" }}
          onClick={() => setIsCall(true)}
          className="px-4 mx-auto my-o"
          disabled={!videoCallData}
        >
          Call
        </Button>
      </div>
      <Modal
        show={isCall}
        onHide={() => setIsCall(false)}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        className="appointment-modal"
        backdrop="static"
      >
        {state === "unregistered" ||
          (callState === "disconnect" && <Modal.Header closeButton />)}
        <Modal.Body>
          <div className="d-flex justify-content-center">
            <img
              src={IMAGES.TWILIO_LOGO}
              alt="Twilio Logo"
              width={120}
              height="auto"
              className={`${
                callState !== "disconnect" &&
                state !== "unregistered" &&
                state !== "error"
                  ? "rotate-img"
                  : ""
              }`}
            />
          </div>
          <Form.Group className="mb-2" controlId="numberField">
            <Form.Label className="fw-bold">Enter Phone Number</Form.Label>
            <Form.Control
              type="text"
              placeholder="+1 530 673 3342"
              size="lg"
              {...register("toNumber", { required: true })}
              defaultValue={patientAppointedDetail?.detailDTO?.phoneNumber}
              disabled
            />
          </Form.Group>
          {state === "error" ? (
            <p className="mb-0" style={{ fontSize: "14px", color: "#E31E27" }}>
              Unable to Connect Server
            </p>
          ) : (
            ""
          )}
          <div className="d-flex">
            {callState === "ringing" ? (
              <p className="mb-0 mx-auto my-0" style={{ color: "#44BC19" }}>
                Ringing ...
              </p>
            ) : state === "registering" ? (
              <p className="mb-0 mx-auto my-0" style={{ color: "#FFB400" }}>
                Registring ...
              </p>
            ) : (
              <Button
                style={{
                  background: "transparent",
                  border: "none",
                  color: "#E31E27",
                }}
                className="fw-bold px-4 mx-auto my-0 cursor-pointer"
                onClick={
                  state === "unregistered" ||
                  callState === "disconnect" ||
                  state === "error"
                    ? startupClient
                    : handleHangUp
                }
              >
                {state === "unregistered" ||
                callState === "disconnect" ||
                state === "error"
                  ? "Call"
                  : "Hang Up"}
              </Button>
            )}
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default CallButton;
